import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'dva';
import { Card, Divider } from 'antd';
import DescriptionList from 'ant-design-pro/lib/DescriptionList';
import LongDesc from '../../../components/LongDesc';
import { showState } from '../../../utils/filter';
import styles from './Show.less';

class Basic extends React.PureComponent {
  // constructor(props) {
  //   super(props);

  //   this.onLoad = this.onLoad.bind(this);
  //   this.onRefresh = this.onRefresh.bind(this);
  //   this.onSearch = this.onSearch.bind(this);
  // }
  // onLoad() {
  //   this.props.dispatch({ type: 'shows/load' });
  // }
  // onRefresh() {
  //   this.props.dispatch({ type: 'shows/reload' });
  // }
  // onSearch(keyword) {
  //   this.props.dispatch({ type: 'shows/search', payload: keyword });
  // }
  render() {
    const { info } = this.props;
    if (!info) {
      return null;
    }
    return (
      <Card bordered={false}>
        <DescriptionList size="large" title="" >
          <DescriptionList.Description term="">
            {'' === info.camera_uuid ? <span className={styles.italic}></span> : info.camera_name}
          </DescriptionList.Description>
          <DescriptionList.Description term="">{info.record_enabled ? '' : ''}</DescriptionList.Description>
          <DescriptionList.Description term="">{showState(info.state)}</DescriptionList.Description>
        </DescriptionList>
        <Divider />
        <DescriptionList size="large" title="" col="1">
          <DescriptionList.Description term="">{`${info.project_name}/${info.uuid}`}</DescriptionList.Description>
          <DescriptionList.Description term=""></DescriptionList.Description>
        </DescriptionList>
        <Divider />
        <DescriptionList size="large" title="" col="1">
          <DescriptionList.Description term="">
            <LongDesc text={info.long_desc} />
          </DescriptionList.Description>
          <DescriptionList.Description term="">
            <LongDesc text={info.comment} emptyText="" />
          </DescriptionList.Description>
        </DescriptionList>
      </Card>
    );
  }
}

Basic.propTypes = {
  info: PropTypes.object,
};

export default connect(state => ({
  info: state.shows.info,
}))(Basic);



